import "../../assets/styles/header.css";
import "../../assets/styles/navbar.css";

import React, { Component } from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import { Link as LinkScroller } from "react-scroll";
import { Element as ScrollElement } from "react-scroll";
import { Trans } from "react-i18next";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import i18next from "i18next";

class Navbar extends Component {
  state = {
    language: i18next.language,
    isSticky: false,
  };
  componentDidMount() {
    const { handleScroll } = this;
    window.addEventListener("scroll", handleScroll);
  }
  componentWillUnmount() {
    const { handleScroll } = this;
    window.removeEventListener("scroll", handleScroll);
  }
  handleChangeLanguage = (e) => {
    let newlang = e.target.getAttribute("value");
    this.setState({ language: newlang });
    i18next.changeLanguage(newlang);
  };
  handleScroll = () => {
    const { isSticky } = this.state;
    const newIsSticky = window.pageYOffset > 0;
    if (isSticky === newIsSticky) return;
    this.setState({ isSticky: newIsSticky });
  };
  render() {
    // const { handleChangeLanguage } = this;
    const {
      // language,
      isSticky,
    } = this.state;

    return (
      <header>
        <ScrollElement name='home'></ScrollElement>

        <nav
          className={`navbar fixed-top ${
            isSticky ? "text-dark bg-light shadow" : "text-light"
          } navbar-expand-lg`}
        >
          <div className='container'>
            <Link className={`navbar-brand ${isSticky ? "link-dark" : "link-light"}`} to='/'>
              <strong>Aygün</strong> Cengiz
            </Link>
            <button
              className='navbar-toggler'
              type='button'
              data-bs-toggle='collapse'
              data-bs-target='#navbarNav'
              aria-controls='navbarNav'
              aria-expanded='false'
              aria-label='Toggle navigation'
            >
              <FontAwesomeIcon icon={faBars} className={isSticky ? "link-dark" : "link-light"} />
            </button>
            <div
              className={`collapse navbar-collapse justify-content-end ${
                !isSticky ? "navbar-colorful-bg" : ""
              }`}
              id='navbarNav'
            >
              <ul className='navbar-nav'>
                <li className='nav-item text-center'>
                  <LinkScroller
                    to='home'
                    spy={true}
                    smooth={true}
                    offset={0}
                    duration={0}
                    className={`nav-link ${isSticky ? "link-dark" : "link-light"} active`}
                  >
                    {/* <Link to='/' className={`nav-link ${isSticky ? 'link-dark' : 'link-light'} active`}> */}
                    <Trans>home</Trans>
                    {/* </Link> */}
                  </LinkScroller>
                </li>
                <li className='nav-item text-center'>
                  <LinkScroller
                    to='aboutme'
                    spy={true}
                    smooth={true}
                    offset={-80}
                    duration={0}
                    className={`nav-link ${isSticky ? "link-dark" : "link-light"}`}
                  >
                    <Trans>aboutme</Trans>
                  </LinkScroller>
                </li>
                <li className='nav-item text-center'>
                  <LinkScroller
                    to='references'
                    spy={true}
                    smooth={true}
                    offset={-200}
                    duration={0}
                    className={`nav-link ${isSticky ? "link-dark" : "link-light"}`}
                  >
                    <Trans>references</Trans>
                  </LinkScroller>
                </li>
                <li className='nav-item text-center'>
                  <LinkScroller
                    to='skills'
                    spy={true}
                    smooth={true}
                    offset={-100}
                    duration={0}
                    className={`nav-link ${isSticky ? "link-dark" : "link-light"}`}
                  >
                    <Trans>skills</Trans>
                  </LinkScroller>
                </li>

                {/* <li className='nav-item dropdown text-center'>
                  <div className='nav-link dropdown-toggle' id='navbarDarkDropdownMenuLink' role='button' data-bs-toggle='dropdown' aria-expanded='false'>
                    {language}
                  </div>
                  <ul className='dropdown-menu' aria-labelledby='navbarDarkDropdownMenuLink'>
                    <li>
                      <div className={`dropdown-item ${language === 'en' ? 'active' : ''}`} onClick={handleChangeLanguage} value='en'>
                        en
                      </div>
                    </li>
                    <li>
                      <div className={`dropdown-item ${language === 'tr' ? 'active' : ''}`} onClick={handleChangeLanguage} value='tr'>
                        tr
                      </div>
                    </li>
                  </ul>
                </li> */}
              </ul>
            </div>
          </div>
        </nav>
        <div className='head-bg'></div>
      </header>
    );
  }
}

export default Navbar;
