import "../../../assets/styles/aboutme.css";

import React, { Component } from "react";
import { Trans, withTranslation } from "react-i18next";
import { faCheck, faEnvelope } from "@fortawesome/free-solid-svg-icons";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Element as ScrollElement } from "react-scroll";
import aboutImage from "../../../assets/images/about1.png";
import { faWhatsapp } from "@fortawesome/free-brands-svg-icons";

class Aboutme extends Component {
  render() {
    return (
      <section id='aboutme' className='pt-5 mt-5'>
        <ScrollElement name='aboutme'>
          <div className='container bg-light my-5 card'>
            <div className='row'>
              <div className='col-md-5 d-flex justify-content-center align-items-center'>
                <img id='profileImage' src={aboutImage} alt='aygun cengiz' height='400' />
              </div>

              <div className='col-md-7 mt-md-5 pb-3'>
                <div className='profile-preword'>
                  <span>Hello!</span>
                </div>

                <div className='profile-info'>
                  <h1 className='profile-title mt-5 pt-2 display-6'>
                    <span>
                      <Trans>Im</Trans>
                    </span>
                    <strong> Aygün Cengiz</strong>
                  </h1>
                  <p>
                    <Trans>engteacher</Trans>
                  </p>
                  <p>
                    <Trans>aboutme_p</Trans>
                  </p>
                  <p>
                    "<Trans>motto</Trans>"
                  </p>
                  <div id='more-info' className='text-uppercase my-2'>
                    <div className='row my-2'>
                      <div className='col-3 my-2'>
                        <strong>
                          <Trans>adress</Trans>
                        </strong>
                      </div>
                      <div className='col-9 my-2'>Istanbul, Turkey</div>

                      <div className='col-3 my-2'>
                        <strong>
                          <Trans>email</Trans>
                        </strong>
                      </div>
                      <div className='col-9 my-2'>
                        <a
                          href='mailto:aygun.cengiz@gmail.com'
                          className='link-dark text-lowercase text-decoration-none'
                        >
                          aygun.cengiz@gmail.com
                        </a>
                      </div>

                      <div className='col-3 my-2'>
                        <strong>
                          <Trans>phone</Trans>
                        </strong>
                      </div>
                      <div className='col-9 my-2'>
                        <a
                          href='https://wa.me/905360137382'
                          className='link-dark text-lowercase text-decoration-none'
                        >
                          +905360137382
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='row'>
              <div id='social-links' className='d-flex justify-content-evenly align-items-center'>
                {/* <a href='https://' alt='aygün cengiz instagram' className='link-light d-flex justify-content-center align-items-center'>
                  <FontAwesomeIcon icon={faInstagram} />
                </a>
                <a href='https://' alt='aygün cengiz linkedin' className='link-light d-flex justify-content-center align-items-center'>
                  <FontAwesomeIcon icon={faLinkedinIn} />
                </a> */}
                <a
                  href='mailto:aygun.cengiz@gmail.com'
                  alt='aygün cengiz mail'
                  className='link-light d-flex justify-content-center align-items-center'
                >
                  <FontAwesomeIcon icon={faEnvelope} />
                </a>
                <a
                  href='https://wa.me/905360137382'
                  alt='aygün cengiz whatsapp'
                  className='link-light d-flex justify-content-center align-items-center'
                >
                  <FontAwesomeIcon icon={faWhatsapp} />
                </a>
              </div>
            </div>
          </div>
          <div className='container my-5'>
            <div id='more-info'>
              <h1>
                <Trans>moreinfo_h1</Trans>
              </h1>
              <p>
                <Trans>moreinfo_p</Trans>
              </p>
              <ul className='fa-ul'>
                <li>
                  <span className='fa-li'>
                    <FontAwesomeIcon icon={faCheck} />
                  </span>
                  <Trans>moreinfo_li2</Trans>
                </li>
                <li>
                  <span className='fa-li'>
                    <FontAwesomeIcon icon={faCheck} />
                  </span>
                  <Trans>moreinfo_li3</Trans>
                </li>
                <li>
                  <span className='fa-li'>
                    <FontAwesomeIcon icon={faCheck} />
                  </span>
                  <Trans>moreinfo_li4</Trans>
                </li>
                <li>
                  <span className='fa-li'>
                    <FontAwesomeIcon icon={faCheck} />
                  </span>
                  <Trans>moreinfo_li5</Trans>
                </li>
              </ul>
            </div>
          </div>
        </ScrollElement>
      </section>
    );
  }
}
export default withTranslation()(Aboutme);
